export default {
	data() {
		return {
			imgList: [],
			imgListFinish:[],
			timeLineActive1: 'item-active text-orange',
			timeLineActive2: 'item-active text-orange',
			timeLineActive3: '',
			timeLineActive4: ''
		};
	},
	methods:{
		ChooseImage() {
			uni.chooseImage({
				count: 5, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['camera ','album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '召唤师',
				content: '确定要删除这段回忆吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		ChooseImageFinish() {
			uni.chooseImage({
				count: 5, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['camera ','album'], //从相册选择
				success: (res) => {
					if (this.imgListFinish.length != 0) {
						this.imgListFinish = this.imgListFinish.concat(res.tempFilePaths)
					} else {
						this.imgListFinish = res.tempFilePaths
					}
				}
			});
		},
		ViewImageFinish(e) {
			uni.previewImage({
				urls: this.imgListFinish,
				current: e.currentTarget.dataset.url
			});
		},
		DelImgFinish(e) {
			uni.showModal({
				title: '召唤师',
				content: '确定要删除这段回忆吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgListFinish.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		}
	}
}
